@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&Roboto:300);
/* Main styles */

/* Variables */

:root {
  --color-win: #78CF82;
  --color-lose: #D94545;
  --color-start: #5b85b7;
  --color-neutral: #445069;
  --color-neutral-light: #D2D2D2;
  --color-keys: #37474F;
  --color-keys-light: #E5E5E5;
  --font-size-large: 60px;
  --font-size-medium: 20px;
  --transition: all .2s ease-in-out;
}

/* main */

* {
  box-sizing: border-box;
}

body {
  justify-content: space-evenly;
  flex-direction: column;
  display: flex;
  height: 100vh;
}

li, ol, ul {
  padding: 0;
  display: inline-block;
}

h2 {
  margin: 0;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-size: 60px;
  font-size: var(--font-size-large);
  margin: 0;
}

button {
  margin: 6px;
  border: 1px solid transparent;
  padding: .5rem 1rem;
  color: #37474F;
  color: var(--color-keys);
  font-size: 20px;
  font-size: var(--font-size-medium);
  background-color: #E5E5E5;
  background-color: var(--color-keys-light);
  border-radius: 5px;
  transition: all .2s ease-in-out;
  transition: var(--transition);
}

.main-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  max-height: 900px;
  text-align: center;
}

.section {
  width: 100%;
  padding: 10px;
}

/* Title */

.header {
  color: #5b85b7;
  color: var(--color-start);
}

/* Phrase */

.letter {
  font-size: 20px;
  font-size: var(--font-size-medium);
  height: 65px;
  width: 65px;
  padding: 20px 1px 0 1px;
  margin-right: 2px;
  color: transparent;
  background: #D2D2D2;
  background: var(--color-neutral-light);
  border-radius: 5px;

  /* To allow animation when a letter is "shown" */
  transition: all 1s;
}

.space {
  width: 15px;
}

.show {
  color: #000;
  background-color: #76CE82;
  transform: rotate(360deg);
}

/* Keyboard */

.keyrow {
  display: flex;
  justify-content: center;
}

.chosen {
  background: #445069;
  background: var(--color-neutral);
  border-radius: 5px;
  font-size: 20px;
  font-size: var(--font-size-medium);
  color: #FFFFFF;
}

.btn__reset {
  cursor: pointer;
  padding: 8px;
  margin: 50px auto;
  background: #FFFFFF;
  border-radius: 5px;
  font-size: 20px;
  font-size: var(--font-size-medium);
}

/* Overlay */

#overlay {
  display: flex;
  flex-direction: column;
  position: fixed;
  color: #FFFFFF;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 5px;
  z-index: 10000;
}

.title {
  color: #FFF;
}

.start {
  background-color: #5b85b7;
  background-color: var(--color-start);
}

.start button {
  color: #78CF82;
  color: var(--color-win);
}

.win {
  background-color: #78CF82;
  background-color: var(--color-win);
}

.win a {
  color: #78CF82;
  color: var(--color-win);
}

.lose {
  background-color: #D94545;
  background-color: var(--color-lose);
}

.lose a {
  color: #D94545;
  color: var(--color-lose);
}

